@media all and (max-width: 768px) {
  body {
    font-size: 14pt !important;
  }
  ol, ul {
    list-style: outside !important;
  }
  li {
    margin: 0.5rem 0 0 2rem;
  }
  .App-header {
    width: 90%;
  }
  .main {
    margin: 2rem 0 0 0;
    width: 90%;
    max-width: 768px;
  }
  .main>div {
    margin-bottom: 3rem;
  }
  .main>.curriculum-vitae>h3 {
    margin-top: 3rem;
  }
  .main>.curriculum-vitae>ol>ul {
