    margin-top: 1rem;
    width: 250px;
    height: 450px;
  }
  @media all and (max-width: 420px) {
    .App-header>.top>ul {
      margin-left: 2rem;
    }
    .footer {
      height: 8rem;
      min-height: 8rem;
    }
    .footer>.logo {
      display: none;
    }
    @media all and (max-width: 375px) {
      @media all and (max-width: 360px) {
        @media all and (max-width: 320px) {
          body {
            font-size: 12pt !important;
          }
          li {
            margin: 0.5rem 0 0 1rem;
          }
        }
      }
    }
  }
}
